import axios from '@/libs/api.request'

/**
 *  报告模板列表
 * @param name 名称
 * @param pageNumber 分页码
 * @param pageSize 分页数
 * @returns {*}
 */
export const getReportTemplateList = ({ name, type, pageNumber, pageSize }) => {
    const data = {
        name,
        pageNumber,
        pageSize,
        type
    };
    return axios.request({
        url: '/template/pageList',
        params: data,
        method: 'get'
    })
};

/**
 * 删除模板
 * @param id
 * @returns {*}
 */
export const deleteReportTemplate = ({ id }) => {
    const data = {

    };
    return axios.request({
        url: '/template/del/' + id,
        params: data,
        method: 'get'
    })
};
/**
 * 获取模板信息
 * @param id
 * @returns {*}
 */
export const getReportTemplateInfo = ({ id }) => {
    const data = {

    };
    return axios.request({
        url: '/template/' + id,
        params: data,
        method: 'get'
    })
};

/**
 *
 * @param name
 * @param type
 * @param content
 * @param templateDetails
 * @returns {*}
 */
export const addReportTpl = ({ name, type, content, templateDetails }) => {
    const data = {
        name,
        type,
        content,
        templateDetails
    };
    return axios.request({
        url: '/template/save',
        data,
        method: 'post'
    })
};

/**
 *
 * @param id
 * @param name
 * @param type
 * @param content
 * @param templateDetails
 * @returns {*}
 */
export const editReportTpl = ({ id, name, type, content, templateDetails }) => {
    const data = {
        id,
        name,
        type,
        content,
        templateDetails
    };
    return axios.request({
        url: '/template/edit',
        data,
        method: 'post'
    })
};


export const deleteReport = (params) => {
    return axios.request({
        url: '/jmreport/deleteReport',
        data: params,
        method: 'post'
    })
};

export const getReportList = (params) => {
    return axios.request({
        url: '/jmreport/fileRecordList',
        params,
        method: 'get'
    })
};


