<template>
  <div class="report-generate">
    <div class="content">
      <div class="header-sub">
        <div class="title">
          <div class="breadcrumb-item-link">报告生成</div>
        </div>
      </div>

      <div style="display:flex">
        <div class="filter-input">
          <Input class="filter-input"
                 v-model="search.type"
                 :maxlength="maxlength"
                 placeholder="请输入报告名称"
                 clearable
                 @on-enter="loadTableData"></Input>
        </div>
        <Button type="default"
                  @click="loadTableData"
                  :loading="pageInfo.loading"
                  custom-icon="iconfont icon-chazhao"
                  style="margin-left:10px"
                  >查询
        </Button>
      </div>
      <div class="user-table">
        <Table class="m-table no-scroll no-border"
               stripe
               :loading="pageInfo.loading"
               :columns="tableColumns"
               :row-class-name="rowClassName"
               @on-row-click="onRowClick"
               :data="tableData"></Table>
      </div>
      <div class="pagination">
        <Page @on-change="onPaginationChange"
              @on-page-size-change="onPaginationSizeChange"
              :current="pageInfo.pageIndex"
              :page-size="pageInfo.pageSize"
              :total="pageInfo.totalElements"
              show-sizer
              show-total />
      </div>
    </div>

    <!-- 新增 -->
    <Drawer v-model="drawer"
            width="720"
            :closable="false"
            :mask-closable="false">
      <div slot="header"
           class="now-form-title">
        <Icon class="now-form-close"
              type="ios-close-circle"
              color="#DCDCDC"
              :size="18"
              @click="drawer = false" />
        <span>{{title}}</span>
      </div>
      <Form v-if="drawer"
            :label-width="$flexible(110)"
            ref="formNew"
            :rules="formValidate"
            :model="form">
        <FormItem :label="formConfig.name.label"
                  prop="name">
          <Input :maxlength="50"
                 placeholder="请输入区域名称"
                 class="search-input"
                 v-model="form.name" />
        </FormItem>
        <FormItem :label="formConfig.type.label">
          <OrgSelect v-model="form.type"
                     placeholder="请选择启动方式"
                     :flag="flag"
                     :id="id"></OrgSelect>
        </FormItem>
      </Form>
      <div class="demo-drawer-footer"
           v-if="$hasAccess('org.edit')">
        <Button style="margin-right: 8px"
                @click="passCancel">取消</Button>
        <Button type="primary"
                @click="confirm">确定</Button>
      </div>
    </Drawer>
  </div>
</template>

<script>
import StatusSelect from '@/components/status-select'
import LaboratoryList from '@/components/laboratory-list'
import OrgSelect from '@c/all-area-select'
import {deleteReport, getReportList} from '@/api/report'
export default {
  name: 'report-list',
  components: {
    StatusSelect,
    LaboratoryList,
    OrgSelect
  },
  i18n_prefix: 'page.report-manage.report-generate',
  data () {
    return {
      search: {
        type: '',
        name: '',
        code: '',
        test: '',
        version: ''
      },
      pageInfo: {
        loading: false,
        pageIndex: 1,
        pageSize: 10,
        totalElements: 30
      },
      tableData: [
        {name: 1}
      ],
      maxlength: 50,
      tableColumns: [
        {
          title: '报告名称',
          key: 'name',
          align: 'left'
        },
        {
          title: '本地地址',
          key: 'path',
          align: 'left'
        },
        // {
        //   title: this.$t('common.text.device_code'),
        //   key: 'deviceCode',
        //   align: 'center',
        // },
        // {
        //   title: this.$t_p('report_name'),
        //   key: 'reportName',
        //   align: 'center',
        // },
        // {
        //   title: this.$t_p('test_type'),
        //   key: 'testType',
        //   align: 'center',
        // },
        // {
        //   title: this.$t_p('report_type'),
        //   key: 'reportType',
        //   align: 'center',
        // },
        // {
        //   title: this.$t_p('report_version'),
        //   key: 'reportVersion',
        //   align: 'center',
        // },
        // {
        //   title: this.$t_p('update_time'),
        //   key: 'updateTime',
        //   align: 'center',
        // },
        {
          title: '操作',
          key: 'action',
          width: 300,
          // fixed: 'right',
          align: 'center',
          render: (h, params) => {
            return h('div', [
              h(
                'span',
                {
                  class: 'primary',
                  style: {
                    color: '#0084FF'
                  },
                  on: {
                    click: () => {
                      this.action(params.row, 'edit')
                    }
                  }
                },
                this.$t_p('edit')
              ),
              h(
                'span',
                {
                  class: 'primary',
                  style: {
                    color: '#0084FF'
                  },
                  on: {
                    click: () => {
                      this.action(params.row, 'putout')
                    }
                  }
                },
                '导出'
              ),
              h(
                'span',
                {
                  class: 'secondary',
                  style: {
                    color: '#F12121'
                  },
                  on: {
                    click: () => {
                      // console.log(params)
                      this.action(params.row, 'delete')
                    }
                  }
                },
                this.$t_p('delete')
              )
            ])
          }
        }
      ],
      drawer: false,
      form: {
        name: '',
        type: ''
      },
      status: '',
      formConfig: {
        name: {
          label: '请确认设备名称'
        },
        type: {
          label: ''
        }
      },
      title: '',
      currentIndex: 0
    }
  },
  mounted () {
    this.loadTableData()
  },
  methods: {
    loadTableData () {
      this.pageInfo.loading = true
      getReportList({pageSize: this.pageInfo.pageSize, pageNumber: this.pageInfo.pageIndex, name: this.search.type}).then(res => {
        if (res.code === 200) {
          this.tableData = res.result.data
          this.pageInfo.totalElements = res.result.totalElement
        }
      }).finally(() => {
        this.pageInfo.loading = false
      })
    },
    addItem () {},
    onRowClick () {},
    onPaginationChange (index) {
      this.pageInfo.pageIndex = index
      this.loadTableData()
    },
    onPaginationSizeChange (size) {
      this.pageInfo.pageSize = size
      this.loadTableData()
    },
    rowClassName (row, index) {
      if (row.id == this.currentIndex) {
        return 'demo-table-info-row'
      } else {

      }
    },
    openDrawer (type) {
      this.drawer = true
      this.status = type
      this.formConfig.type.label =
        this.status === 'start'
          ? '选择启动方式'
          : this.status === 'stop'
            ? '选择停止方式'
            : '选择系统控制参数'
      this.title =
        this.status === 'start'
          ? '启动'
          : this.status === 'stop'
            ? '停止'
            : '测试'
    },
    action (params, type) {
      if (type === 'edit') {
        this.$router.push({
          path: '/report-generate',
          query: {
            id: params.urlId
          }
        })
      } else if (type === 'putout') {
        window.location.href = window.location.origin + '/device-ai/' + params.url
      } else {
        this.$Modal.confirm({
          title: this.$t('common.text.prompt'),
          width: this.$flexible(450),
          content:
          this.$t('common.text.confirm_title', {
            name: '报告名称'
          }) + `[${params.name}]的报告吗?`,
          onOk: () => {
            deleteReport({id: params.id}).then(
              () => {
                this.$Message.success(this.$t('common.message.delete_success'))
                this.loadTableData()
              },
              () => {
                this.$Message.warning(this.$t('common.message.delete_fail'))
              }
            )
          }
        })
      }
    },
    confirm () {},
    passCancel () {}
  }
}
</script>

<style lang="less" scoped>
@import '../../../../style/varient';
@size-padding: 10px;
.report-generate{
  width: 100%;
  height: 100%;
  .content {
    width: 100%;
    height: calc(~'100% - ' 50px);
    box-shadow: 0px -1px 2px 0px rgba(0, 0, 0, 0.05),
      /*上边阴影  红色*/ -1px 0px 2px 0px rgba(0, 0, 0, 0.05),
      /*左边阴影  绿色*/ 1px 0px 2px 0px rgba(0, 0, 0, 0.05),
      /*右边阴影  蓝色*/ 0px 1px 2px 0px rgba(0, 0, 0, 0.05); /*下边阴影  黄色*/
    padding: 24px;
  }
}
</style>



